        </a>

        <router-link v-slot="{navigate,href}" :to="{name:'Blog',query:{u : item.author.id,user:item.author.username},hash:'#topBlog'}"><a @click="navigate" :href="href" class="uppercase text-accent"><span v-if="item.author && item.author.first_name" >{{ item.author.first_name }}</span> <span v-if="item.author && item.author.last_name" >{{ item.author.last_name }}</span> <span v-if="!item.author.last_name && !item.author.first_name" > {{item.author.username}} </span></a>
        </router-link>
      </div>
      <h4 class="blogCard__title fw-600"><h4 class="d-flex align-items-center"><span>{{item.title}}</span>
      </h4>
      </h4>
      <div class="blogCard__text mt-20">

