            </div>
            <div class="authorPost__content">
              <p class="blogSidebar__content-title"><span v-if="item.author && item.author.first_name" >{{ item.author.first_name }}</span> <span v-if="item.author && item.author.last_name" >{{ item.author.last_name }}</span></p>
              <p style="color: #615e5e" v-if="item.author && item.author.bio" >{{item.author.bio}}</p>
              <p style="color: #615e5e" v-else >بدون بیوگرافی</p>
            </div>
          </div>
        </div>

        <v-divider class="mb-16"></v-divider>

        <div v-if="$store.state.userId" >
          <div class="d-flex justify-space-between" >
            <h3  style="font-size: 25px;color: #000">نظر شما <span v-if="comment.is_replay_to" style="font-size: 16px;color: #2e2e2e" >در پاسخ به <span v-if="commentSelected.user && commentSelected.user.first_name && commentSelected.user.last_name" >{{commentSelected.user.first_name + ' ' +commentSelected.user.last_name}}</span><span v-else >{{commentSelected.user.username}}</span> :</span></h3>
            <v-btn v-if="comment.is_replay_to" color="primary" icon @click="comment.is_replay_to=null" ><v-icon color="primary" large >mdi-close</v-icon></v-btn>
          </div>
