              <v-btn height="54" :loading="sendLoading" elevation="0" class="rounded-0 button-w-b" v-if="$store.state.permissions && $store.state.permissions['create-comment']" @click="sendComment()" style="min-width: 13.75rem;padding: 1.25rem 2rem;" dark color="#d7926b" >ارسال نظر</v-btn>
