        </div>

        <h3 class="mt-10 mb-6" style="font-size: 25px;color: #000" v-if="comments && comments.length">نظرات</h3>
        <div id="yourComment" ></div>
        <div v-for="(item,i) in comments" :key="i+'commentsss'" class="comment-box" :class="[item.id==comment.is_replay_to?'comment-box--active' : '']" >
