          <h4 style="cursor: pointer" @click="toSeeProfile(item.user.id)" > <v-avatar v-if="item.user && item.user.avatar" class="ml-1" color="blue" size="36" ><v-img :src="item.user.avatar" ></v-img></v-avatar> <span v-if="item.user && item.user.first_name && item.user.last_name" >{{item.user.first_name + ' ' +item.user.last_name}}</span><span v-else >{{item.user.username}}</span> : </h4>
