          <v-btn v-if="(item.id != comment.is_replay_to) && $store.state.userId && $store.state.permissions && $store.state.permissions['create-comment']" @click="replyToComments(item)" class="comment-send-btn d-block mr-auto mt-3" style="letter-spacing: 0" text dark color="primary" >
            <v-icon class="ml-1" >mdi-comment-multiple-outline</v-icon> ارسال پاسخ
          </v-btn>

          <div class="mt-3" >
          <div class="comment-box-reply" v-for="(rply,i) in item.comment_replies" :key="i+'commentsssre'" >
