            <h4 style="cursor: pointer" @click="toSeeProfile(rply.user.id)" > <v-avatar v-if="rply.user && rply.user.avatar" class="ml-1" color="blue" size="36" ><v-img :src="rply.user.avatar" ></v-img></v-avatar> <span v-if="rply.user && rply.user.first_name && rply.user.last_name" >{{rply.user.first_name + ' ' +rply.user.last_name}}</span><span v-else >{{rply.user.username}}</span> : </h4>
              <p class="ma-0" style="font-size: 12px" v-if="item.date_published_date" >{{ item.date_published_date | momentHour }}</p>
