  </v-card>
    <ProfileDialog :ddata="profileData" color="primary" />
  </div>
</template>

<script>
import axios from "axios";
import {forceRerender} from "@/components/Misc/forceRerender";
import ProfileDialog from "@/components/Misc/ProfileDialog";

export default {
  name: "SingleBlog",
  components: {ProfileDialog},
  mixins : [forceRerender],
  created() {
    this.fetchPost();
  },
  data () {
    return {
      profileData : {
        idSelected : null,
        dialog : false,
      },
      item : {
        title : null
      },
      comments : null,
      comment : {
        post : this.$route.params.id,
        is_replay_to : null,
        content : null,
        enabled : false,
      },
      loading : false,
      sendLoading : false,
      commentSelected : null,
    }
  },
  methods : {
    toSeeProfile (id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    replyToComments (item) {
      this.comment.is_replay_to = item.id;
      this.commentSelected = item;
      this.$router.push({hash:'#yourComment'});
    },
    sendComment(){

      if(this.$store.state.profile && this.$store.state.profile.role==='admin') {
        this.comment.enabled = true;
      }

      this.$refs.form.validate();
      if(this.$refs.form.validate()){
        this.sendLoading = true;
        let comment = {...this.comment};
        this.comment.content = null;
        this.comment.is_replay_to = null;
        this.forceRerender();
        axios.post(this.$store.state.host + '/blog/api/v1/comment/create-comment',comment,{
          headers : {
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(()=>{
          this.notify('نظر شما ثبت شد.','success')
          this.fetchComments();
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.sendLoading = false;
        })
      }
    },
    fetchComments () {
      axios.get(this.$store.state.host + '/blog/api/v1/comment/by-post/'+this.$route.params.id).then(response=>{
        this.comments = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.$store.state.loading=false;
        this.loading = false;
      })
    },
    fetchPost () {
      this.loading = true;
      axios.get(this.$store.state.blogApi + 'post/post-detail/'+this.$route.params.id).then(response=>{
        this.item = response.data.data;
        this.fetchComments();
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    }
  },
  watch : {
    '$route'(){
      this.fetchPost();
    }
  }
}
</script>

